import { Control, Controller, Path, RegisterOptions } from 'react-hook-form';
import { Props as ReactSelectProps } from 'react-select';

// Components
import FormField, { FormFieldProps } from '../FormField';
import Select from '../Select';
import FormReadOnlyFieldTextValue from '../FormReadOnlyFieldTextValue';

// Types
import { TestMetadata } from 'types';

export interface Option {
  value: string;
  label: string;
  isDisabled?: boolean;
}

export interface FormSelectFieldProps<I extends Record<string, unknown>> {
  /** Name for field (Unique in the form) */
  fieldName: Path<I>;
  /** Display name for field */
  labelText?: string;
  /** Tooltip icon and hover text */
  tooltip?: string[];
  /** Options to have in the Select */
  options?: Option[];
  /** Props to spread down to the select */
  selectProps?: ReactSelectProps;
  /** Mark field required (*) */
  required?: boolean;
  /** Placeholder value */
  placeholder?: string;
  /** Initial value selected */
  initialValue?: Option;
  /** Rules to pass down to control */
  rules?: RegisterOptions;
  /** Sets helper */
  helper?: string;
  /** Class name */
  className?: string;
  /** Sets test id */
  testMetadata?: {
    wrapper?: TestMetadata;
    input?: TestMetadata;
  };
  defaultValue?: unknown;
  labelFor?: string;
  /** items can be added to selected options */
  creatable?: boolean;
  /** options can be loaded asynchronously */
  async?: boolean;
}

const FormSelectField = <I extends Record<string, unknown>>({
  fieldName,
  labelText,
  tooltip,
  options,
  selectProps,
  required,
  placeholder,
  initialValue,
  rules,
  testMetadata,
  helper,
  className,
  defaultValue,
  labelFor,
  creatable,
  async,
  readOnly,
  ...otherProps
}: FormSelectFieldProps<I> & Partial<FormFieldProps<I>>): JSX.Element => {
  return (
    <FormField<I>
      labelFor={labelFor}
      labelText={labelText}
      tooltip={tooltip}
      fieldName={fieldName}
      required={required}
      helper={helper}
      className={className}
      readOnly={readOnly}
      control={(formMethods, props) => (
        <Controller
          name={fieldName as string}
          control={formMethods.control as Control<Record<string, unknown>>}
          defaultValue={defaultValue !== undefined ? defaultValue : null}
          rules={{
            required: required ? `${labelText} is required.` : undefined,
            ...rules,
          }}
          render={({ field: { onChange, onBlur, value, name } }) => {
            if (readOnly) {
              return (
                <FormReadOnlyFieldTextValue
                  value={value ? (value as Option).label : undefined}
                  testMetadata={testMetadata?.input}
                />
              );
            }

            return (
              <Select
                inputId={fieldName as string}
                options={options}
                defaultValue={initialValue}
                placeholder={placeholder}
                onChange={onChange}
                onBlur={onBlur}
                value={value as Option}
                name={name}
                creatable={creatable}
                async={async}
                {...testMetadata?.input}
                {...selectProps}
                {...props}
              />
            );
          }}
        />
      )}
      {...testMetadata?.wrapper}
      {...otherProps}
    />
  );
};

export default FormSelectField;
